import React, { useState } from 'react';
import { parsePhoneNumberFromString } from 'libphonenumber-js';
import { User, Mail, Phone, AlertCircle } from 'lucide-react';
import Input from '../common/Input';

const OwnerInfo = ({ data, onUpdate }) => {
  const [errors, setErrors] = useState({ name: '', email: '', phone: '' });

  // --- soft validators ---
  const validateName = (val) => {
    const trimmed = val.trim();
    if (!/^[\u0391-\u03C9a-zA-Z\s]{2,}$/.test(trimmed)) return ' ';
    if (trimmed.split(/\s+/).filter(Boolean).length < 2) return ' + ';
    return '';
  };

  const validateEmail = (val) =>
    /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(val.trim()) ? '' : ' email';

  const validatePhone = (raw) => {
    const phone = parsePhoneNumberFromString(raw, 'GR');
    return phone && phone.isValid() ? '' : ' ';
  };

  // --- handlers ---
  const handleName = (e) => {
    const val = e.target.value;
    onUpdate({ ownerName: val });
    setErrors((s) => ({ ...s, name: validateName(val) }));
  };

  const handleEmail = (e) => {
    const val = e.target.value;
    onUpdate({ ownerEmail: val });
    setErrors((s) => ({ ...s, email: validateEmail(val) }));
  };

  const handlePhone = (e) => {
    const raw = e.target.value;
    onUpdate({ ownerPhone: raw });
    setErrors((s) => ({ ...s, phone: validatePhone(raw) }));
  };

  return (
    <div>
      {/* 1.     */}
      <h2 className="text-2xl font-bold text-slate-700 mb-6 flex items-center gap-2">
        <User className="w-6 h-6 text-sky-600" />
          
      </h2>

      <div className="space-y-5">
        {/* ---  --- */}
        <div>
          <Input
            label=""
            type="text"
            value={data.ownerName}
            onChange={handleName}
            placeholder="..  "
            icon={User}
            required
            invalid={!!errors.name}
          />
          {errors.name && (
            <p className="text-sm text-sky-500 mt-1 flex items-center gap-1">
              <AlertCircle className="w-4 h-4" /> {errors.name}
            </p>
          )}
        </div>

        {/* --- Email --- */}
        <div>
          <Input
            label="Email"
            type="email"
            value={data.ownerEmail}
            onChange={handleEmail}
            placeholder="email@example.com"
            icon={Mail}
            required
            invalid={!!errors.email}
          />
          {errors.email && (
            <p className="text-sm text-sky-500 mt-1 flex items-center gap-1">
              <AlertCircle className="w-4 h-4" /> {errors.email}
            </p>
          )}
        </div>

        {/* ---  --- */}
        <div>
          <Input
            label=""
            type="tel"
            value={data.ownerPhone}
            onChange={handlePhone}
            placeholder="69xxxxxxxx  +30..."
            icon={Phone}
            required
            invalid={!!errors.phone}
          />
          {errors.phone && (
            <p className="text-sm text-sky-500 mt-1 flex items-center gap-1">
              <AlertCircle className="w-4 h-4" /> {errors.phone}
            </p>
          )}
        </div>
      </div>

      {/* 2. Info box   sky-50    */}
      <div className="mt-6 bg-sky-50 rounded-lg p-4 border border-sky-100">
        <p className="text-sm text-slate-700 flex items-center gap-2">
          <Mail className="w-4 h-4 text-sky-400" />
            email     .
        </p>
      </div>
    </div>
  );
};

export default OwnerInfo;